import React, { useContext, useState } from 'react'
import DataContext from '../../context/context'
import { Tab, Tabs, Typography, Box, Button, Grid, Divider } from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles'
import paths from '../../static/paths'

const TabPanel = (props) => {
  const { children, value, index, ...other } = props;
  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`vertical-tabpanel-${index}`}
      aria-labelledby={`vertical-tab-${index}`}
      {...other}
    >
      {value === index && <Box p={3}>{children}</Box>}
    </Typography>
  );
}

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
    display: 'flex',
  },
  tabs: {
    borderRight: `1px solid ${theme.palette.divider}`,
  },
  divider: {
    marginBottom: 8
  }
}));

function a11yProps(index) {
  return {
    id: `vertical-tab-${index}`,
    'aria-controls': `vertical-tabpanel-${index}`,
  };
}

const Register = (props) => {
  const { avalibleShowByDay: shows } = useContext(DataContext)
  const classes = useStyles();
  const [value, setValue] = useState(0)

  const handleChange = (e, newValue) => {
    setValue(newValue)
  };

  return (
    <>
      <Typography variant="h3" component="h2" align='center'>
        Wybierz seans
      </Typography>
      <div className={classes.root}>

        <Tabs
          orientation="vertical"
          variant="scrollable"
          value={value}
          onChange={handleChange}
          aria-label="Vertical tabs example"
          className={classes.tabs}
        >
          {shows.map((show, index) => {
            return (<Tab key={index} label={show.date} {...a11yProps(index)} />)
          })}

        </Tabs>
        {shows.map((show, index) => {
          return (<TabPanel value={value} index={index}>
            <>
              {show.films.map((film, index) => {
                return (<Grid container direction='column' key={`${show.date}-${index}`}>
                  <Grid item> {film.title}</Grid>
                  <Grid item>
                    {film.showTimes.map(s => {
                      return (<Button key={`${s.hour}`} onClick={() => {
                        props.history.push({
                          pathname: `${paths.rooms}/${s.room}`,
                          state: {
                            hour: s.hour,
                            title: film.title,
                            room: s.room,
                            date: show.date
                          }

                        })
                      }}>{s.hour}</Button>)
                    })}
                    <Divider className={classes.divider} />
                  </Grid>
                </Grid>)
              })}
            </>
          </TabPanel>
          )
        })}
      </div>
    </>
  )
};

export default Register;