import React, { useState, useContext, useEffect } from 'react'
import DataContext from '../../context/context'
import chunk from 'lodash/chunk'
import { Grid, Typography, MenuItem, Select, FormControl, InputLabel, Button } from '@material-ui/core'
import { generateSeats, fillBackground, drawRow } from './utils'
import { makeStyles } from '@material-ui/core/styles'
import paths from '../../static/paths'

const useStyles = makeStyles(theme => ({
  container: {
    width: '100%',
    minWidth: 300,
  },
  button: {
    marginTop: 16
  }
}));

const Room = ({ history, location, match }) => {
  const { rooms } = useContext(DataContext)
  const [canvas, setCanvas] = useState(null)
  const [seats, setSeats] = useState([])
  const [title, setTitle] = useState(null)
  const [date, setDate] = useState(null)
  const [hour, setHour] = useState(null)
  const [selected, setSelected] = useState(null)


  const classes = useStyles()

  useEffect(() => {
    setSeats(generateSeats(rooms[match.params.id]))
  }, [match.params.id, rooms])

  useEffect(() => {
    if (location.state) {
      setTitle(location.state.title)
      setDate(location.state.date)
      setHour(location.state.hour)
    }
  }, [location.state])

  useEffect(() => {
    setCanvas(document.getElementById('canvas').getContext('2d'))
  }, []);

  useEffect(() => {
    if (canvas) {
      fillBackground(canvas)
      const { overallPlaces, overallRows } = rooms[match.params.id]
      const chunks = chunk(seats, overallPlaces / overallRows)
      chunks.forEach((ch, index) => drawRow(canvas, ch, index))
    }

  }, [canvas, match.params.id, rooms, seats])

  return (
    <>
      <Grid
        container
        justify='center'
        alignItems='center'
        direction='column'
        className={classes.container}
      >
        {title && (
          <>
            <Grid item>
              <Typography variant="h3" component="h1" align='center'>
                {title}
              </Typography>
            </Grid>
            <Grid item>
              <Typography variant="h4" component="h2" align='center'>
                {date} {hour}
              </Typography>
            </Grid>
          </>
        )}
        <Grid item>
          <canvas
            id='canvas'
            width='410'
            height='360'
            style={{ marginTop: 16 }}
          />
        </Grid>

        {title && (
          <>
            <Grid item >
              <FormControl className={classes.container}>
                <InputLabel id="demo-simple-select-helper-label">Wybierz miejsce siedzące</InputLabel>
                <Select
                  labelId="demo-simple-select-label"
                  id="demo-simple-select"
                  value={selected}
                  onChange={(e) => setSelected(e.target.value)}
                >
                  {seats.map(seat => (
                    <MenuItem key={seat} value={seat}>{seat}</MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            <Grid item>
              <Button
                variant='contained'
                color='primary'
                disabled={!selected}
                className={classes.button}
                onClick={() => {
                  history.push({
                    pathname: paths.ticket,
                    state: {
                      ...location.state,
                      seatId: selected,
                    }
                  })
                }
                }
              > Kup Bilet</Button>
            </Grid>
          </>
        )}
      </Grid>

    </>
  );
};

export default Room;