export const getAvaliableShowsByDay = (movies, showing) => {
  const days = new Set(showing.map(s => s.date))

  const shows = []
  days.forEach((day) => {
    const out = {}
    out.date = day
    out.films = movies.map((movie) => {
      const t = {}
      t.title = movie.title
      t.showTimes = showing.filter((s) => s.title === movie.title && s.date === day).map(show => ({
        hour: show.hour, room: show.cinemaRoomId
      }))
      return t
    })
    shows.push(out)
  })
  return shows
}